import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';

export default function PatientPage() {
  const navigate = useNavigate();

  const [patients, setPatients] = useState([]);

  useEffect(() => {
    // Fetch patient data from the server
    axios.get('/getPatients')
      .then(response => setPatients(response.data.patients))
      .catch(error => console.error(error));
  }, []);

  return (
    <div className="container mx-auto my-8">
      <div className="mb-8 flex justify-between items-center">
        <h1 className="text-4xl font-bold mb-4"><Link to="/appointmentform">Patient Page</Link></h1>
        <div>
          <button className="bg-blue-900 text-white px-4 py-2 rounded-md hover:bg-blue-600">
            <Link to='/patientform'>Add Patient</Link>
          </button>
        </div>
      </div>
      <hr />

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {patients.map((patient, index) => (
          <div key={patient._id} className={`bg-${index % 2 === 0 ? 'gray-100' : 'gray-200'} shadow-md p-6 rounded-md mb-4`}>
            <h2 className="text-xl font-semibold mb-2">{patient.name}</h2>
            <p className="text-gray-700 mb-2">{patient.contact}</p>
            <p className="text-gray-700 mb-2">{patient.contactDetails}</p>
            <p className="text-gray-700">{patient.medicalHistory}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
