import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { AiOutlineDelete } from 'react-icons/ai';

const AppointmentPage = () => {
  const [appointments, setAppointments] = useState([]);

  useEffect(() => {
    // Fetch appointment data from the server
    axios.get('/getAppointments')
      .then(response => setAppointments(response.data.appointments))
      .catch(error => console.error(error));
  }, []);

  const handleDelete = async (id) => {
    try {
      // Make a DELETE request to your server
      await axios.delete(`/deleteAppointment/${id}`);
      
      // Update the state to reflect the deleted appointment
      setAppointments(prevAppointments => prevAppointments.filter(appointment => appointment._id !== id));
      
      console.log(`Appointment with ID ${id} deleted successfully.`);
    } catch (error) {
      console.error(`Error deleting appointment with ID ${id}:`, error);
    }
  };

  return (
    <div className="container mx-auto my-8">
      <div className="mb-8 flex flex-col md:flex-row justify-between items-center">
        <h1 className="text-4xl font-bold mb-4 md:mb-0">
          <Link to="/appointmentform">Appointment Page</Link>
        </h1>
        <div>
          <button className="bg-blue-900 text-white px-4 py-2 rounded-md hover:bg-blue-600">
            <Link to='/appointmentform'>Add Appointment</Link>
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {appointments.map((appointment, index) => (
          <div key={appointment._id} className={`bg-${index % 2 === 0 ? 'gray-100' : 'gray-200'} shadow-md p-6 rounded-md mb-4 relative`}>
            <h2 className="text-xl font-semibold mb-2">Appointment Title: <br /> {appointment.notes}</h2>
            <p className="text-gray-700 mb-2">Doctor: {appointment.doctorId}</p>
            <p className="text-gray-700 mb-2">Patient: {appointment.patientId}</p>
            <p className="text-gray-700 mb-2">Date and Time: {new Date(appointment.dateTime).toLocaleString()}</p>
            <button
              className="absolute top-2 right-2 cursor-pointer"
              onClick={() => handleDelete(appointment._id)}
            >
              <AiOutlineDelete size={24} color="#FF0000" />
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AppointmentPage;
