import React from 'react';
import { Link } from 'react-router-dom';
import Welcome from '../../Asset/Images/Welcome.png';

const Home = () => {
  const cardsData = [
    {
      id: 1,
      image: 'https://chavanhospital.in/wp-content/uploads/2021/05/baby-at-doctor-1-72dpi-1.jpg',
      title: 'Children Specialist',
      description: 'Expert care for your little ones. Our Children Specialist services focus on providing top-notch healthcare for children, ensuring their well-being and happiness.',
    },
    {
      id: 2,
      image: 'https://www.columbiaasia.com/malaysia/sites/default/files/images/specialties-cardiology-interventional-cardiology.jpg',
      title: 'Cardiology Services',
      description: 'Comprehensive heart care. Our Cardiology Services are dedicated to keeping your heart healthy and strong. Discover personalized care and advanced treatments.',
    },
    {
      id: 3,
      image: 'https://www.brrh.com/images/design/subpage-banners/orthopedics-banner.jpg',
      title: 'Orthopedic Care',
      description: 'Move freely with Orthopedic expertise. Our Orthopedic Care services focus on musculoskeletal health, ensuring you lead an active and pain-free life.',
    },
    {
      id: 4,
      image: 'https://health.usf.edu/-/media/Images/Medicine/Graduate-Affairs/womenshealth.ashx?h=422&w=997&hash=26E5BC87C92C7CF777A43E85A0612E8DEAA32CAC',
      title: 'Women’s Health',
      description: 'Empowering women through health. Our Women’s Health services are designed to address unique health needs, providing support and expert care at every stage.',
    },
  ];

  return (
    <div>
      <div className="container mx-auto mt-10 md:flex md:items-center">
        <div className="mb-8 md:w-1/2">
          <img src={Welcome} alt="Welcome Image" className="w-full h-auto rounded-md" />
        </div>
        <div className="md:w-1/2 md:pl-8">
          <h1 className="text-4xl font-bold mb-4">Welcome to Doctor Appointment System</h1>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed et quam id justo consectetur vestibulum.
            Vivamus egestas ligula vel felis congue, eu aliquam elit auctor. Proin eget fermentum urna.
          </p>
          <p>
            Curabitur rhoncus, urna nec varius viverra, neque quam dignissim arcu, vitae dapibus quam massa
            vel dolor. Nulla facilisi. Suspendisse potenti. Sed facilisis convallis nunc, eget facilisis odio
            hendrerit nec.
          </p>
          <div className="mt-8">
            <Link to="/next-page" className="bg-blue-900 text-white px-4 py-2 rounded-md hover:bg-blue-600">
              Go to Next Page
            </Link>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 gap-4">
        {cardsData.map((card) => (
          <div key={card.id} className="bg-white shadow-md p-6 rounded-md mb-4 text-center md:w-full mx-auto">
            <img
              src={card.image}
              alt={`Card ${card.id}`}
              className="w-full h-32 object-cover mb-4 rounded-md"
            />
            <h2 className="text-xl font-semibold mb-2">{card.title}</h2>
            <p className="text-gray-700 mb-2">{card.description}</p>
            <button className="bg-blue-900 text-white px-4 py-2 rounded-md hover:bg-blue-600">
              Learn More
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Home;
