import { message } from 'antd';
import axios from 'axios';
import React, { useState } from 'react';

const Appointment = () => {
  const [formData, setFormData] = useState({
    doctorId: '',
    patientId: '',
    dateTime: '',
    notes: '',
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    
    axios.post('/addAppointment', formData)
      .then(response => {
        console.log(response.data);
        message.success('Appointment set successfully!');
        // You can handle success as needed
      })
      .catch(error => {
        console.error(error);
        message.error('Failed to set appointment. Please try again.');
      });
  };

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <div className="container mx-auto my-8">
      <h1 className="text-4xl font-bold mb-4">Set Appointment</h1>
      <form onSubmit={handleSubmit} className="bg-white p-6 rounded shadow-lg">
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="doctorId">
            Doctor ID:
          </label>
          <input
            type="number"
            name="doctorId"
            value={formData.doctorId}
            onChange={handleChange}
            className="border rounded w-full py-2 px-3"
            placeholder="Enter doctor ID"
            required
          />
        </div>
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="patientId">
            Patient ID:
          </label>
          <input
            type="number"
            name="patientId"
            value={formData.patientId}
            onChange={handleChange}
            className="border rounded w-full py-2 px-3"
            placeholder="Enter patient ID"
            required
          />
        </div>
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="dateTime">
            Appointment Date and Time:
          </label>
          <input
            type="datetime-local"
            name="dateTime"
            value={formData.dateTime}
            onChange={handleChange}
            className="border rounded w-full py-2 px-3"
            required
          />
        </div>
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="notes">
            Notes:
          </label>
          <textarea
            name="notes"
            value={formData.notes}
            onChange={handleChange}
            className="border rounded w-full py-2 px-3"
            placeholder="Enter notes"
            required
          />
        </div>
        <button
          type="submit"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
        >
          Set Appointment
        </button>
      </form>
    </div>
  );
};

export default Appointment;
