import React, { useState } from 'react';
import axios from 'axios';
import { message } from 'antd';

const PatientForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    contactDetails: '',
    medicalHistory: '',
  });

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);

    // Send the patient data to the server
    axios.post('/addPatient', formData)
      .then(response => {
        console.log(response.data); // You can handle success as needed
        message.success("Patient is Added Successfully")
      })
      .catch(error => {
        console.error(error);
        message.error("Patient is Not Added Successfully")
      });
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen">
      <div className="bg-white w-full md:w-1/2 lg:w-1/3 p-8 rounded shadow-md">
        <h1 className="text-4xl font-bold mb-4">Add Patient</h1>
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label htmlFor="ID" className="block text-sm font-medium text-gray-700">
              ID
            </label>
            <input
              type="number"
              id="id"
              name="_id"
              value={formData._id}
              onChange={handleChange}
              className="mt-1 p-2 border border-gray-300 rounded-md w-full"
              required
            />
          </div>
          <div className="mb-4">
            <label htmlFor="name" className="block text-sm font-medium text-gray-700">
              Name
            </label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              className="mt-1 p-2 border border-gray-300 rounded-md w-full"
              required
            />
          </div>

          <div className="mb-4">
            <label htmlFor="contactDetails" className="block text-sm font-medium text-gray-700">
              Contact Details
            </label>
            <input
              type="text"
              id="contactDetails"
              name="contactDetails"
              value={formData.contactDetails}
              onChange={handleChange}
              className="mt-1 p-2 border border-gray-300 rounded-md w-full"
            />
          </div>

          <div className="mb-4">
            <label htmlFor="medicalHistory" className="block text-sm font-medium text-gray-700">
              Medical History
            </label>
            <textarea
              id="medicalHistory"
              name="medicalHistory"
              value={formData.medicalHistory}
              onChange={handleChange}
              className="mt-1 p-2 border border-gray-300 rounded-md w-full"
            />
          </div>

          <div>
            <button
              type="submit"
              className="bg-blue-900 text-white px-4 py-2 rounded-md hover:bg-blue-600"
            >
              Add Patient
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default PatientForm;
