import React from 'react';
import { Link } from 'react-router-dom';
// import Logo from './path-to-your-logo-file';  // Replace with the actual path to your logo file
import logo from '../../Asset/Images/logo.png'
function Sidebar() {
  return (
    <div className="bg-gray-800 text-white h-16 fixed top-0 w-full flex items-center justify-between px-4">
      <div className="flex items-center space-x-4">
        <img src={logo} alt="Logo" className="h-10 w-10" />  {/* Adjust the height and width as needed */}
        <h1 className="text-2xl font-semibold">DAS</h1>
      </div>
      <div className="p-4">
        <ul className="flex space-x-4 mt-4">
          <li>
            <Link to="/" className="text-gray-300 hover:text-white flex items-center">
              Main
            </Link>
          </li>
          <li>
            <Link to="/doctor" className="text-gray-300 hover:text-white flex items-center">
              Doctors
            </Link>
          </li>
          <li>
            <Link to="/patient" className="text-gray-300 hover:text-white flex items-center">
              Patients
            </Link>
          </li>
          <li>
            <Link to="/appointment" className="text-gray-300 hover:text-white flex items-center">
              Appointments
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Sidebar;
